.work__filters {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    column-gap: 1.875rem;
    margin-bottom: 2.5rem;
}

.work__item {
    cursor: pointer;
    font-weight: var(--font-bold);
    transition: .3s;
}

.work__item:hover {
    color: var(--first-color)
}

.work__container {
    grid-template-columns:repeat(3, 1fr);
    gap: 1.875rem;
}

.work__card {
    position: relative;
    overflow: hidden;
    border-radius: var(--border-radius);
    box-shadow: var(--shadow)
}
.work__mask {
    background: palevioletred;
    position: absolute;
    left:0;
    top: 0;
    height: 100%;
    width: 100%;
    transition: .3s;
    opacity: 0;
}

.work__card:hover .work__mask {
    opacity: .9;
}

.work__category {
    color: #fff;
    background-color: var(--first-color);
    border-bottom-left-radius: .98rem;
    border-bottom-right-radius: .98rem;
    position: absolute;
    top: 0;
    left: 1.5rem;
    font-size: var(--small-font-size);
    display: inline-block;
    padding: .19rem .625rem;
    transform: translateY(-40px);
    transition: .3s;
}

.work__title {
    color: #fff;
    font-size: var(--h3-font-size);
    margin: 0 0 0 .98rem;
    padding: 0 1.25rem;
    position:absolute;
    top: 3.75rem;
    transform: translateY(30px);
    transition: .3s ;
    opacity: 0;
}
.work__button {
    color: #000000;
    position: absolute;
    bottom: 1.5rem;
    left:1.5rem;
    font-size: var(--h3-font-size);
    display: block;
    background-color: powderblue;
    height: 100px;
    width: 300px;
    cursor: pointer;
    border-radius: 50%;
    text-align: center;
    line-height: 42px;
    transition: .3s;
    opacity: 0;
}

.work__card:hover .work__title {
    opacity: 1;
}

.work__card:hover .work__title,
.work__card:hover .work__category {
    opacity: 1;
    transform: translateY(0);
}
@media screen and (max-width: 1024px) {
    .work__filters {
        justify-content: center;
    }

    .work__container {
        grid-template-columns: repeat(2, 330px);
        justify-content: center;
        row-gap: 1.875rem;
    }
}

@media screen and (max-width: 760px) {
    .work__filters {
        row-gap: .25rem;
    }

    .work__container {
        grid-template-columns: repeat(2, 330px);
        justify-content: center;
        row-gap: 1.875rem;
    }
}

@media screen and (max-width: 350px) {

    .work__container {
        grid-template-columns: 1fr;
    }
}

